.default {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.list {
  margin-top: 10px;
  overflow-y: auto;
}
.listItem {
  padding: 6px 10px;
  background-color: #cfdafe;
  &:not(:first-child) {
    margin-top: 4px;
  }
  .highlighted {
    color: red;
  }
}
.itemHeader {
  display: flex;
}
.itemId {
  margin-bottom: 0;
  opacity: 0.5;
}
.status {
  text-align: center;
}
